<template>
    <div class="text-green-900">
        <!-- Navber -->
        <TheNavber @filterActivity="filterActivityHandler" />
        <!-- End Navber -->

        <section class="flex grid grid-cols-1 md:grid-cols-4 md:space-x-4 p-4 md:py-8 md:px-16">
            <!-- New Activity Form -->
            <ActivityForm @createActivity="createdActivity" :categories="categories" />
            <!-- End New Activity Form -->

            <!-- Activity section -->
            <div class="flex flex-col space-y-4 md:flex-col col-span-1 md:col-span-3">
                <div class="bg-green-600 py-2 px-4 text-green-100 rounded-lg flex justify-between items-center">
                    <span>Currently {{ filterData.length }} activities </span>
                    <span>{{ activityMessage() }}</span>
                </div>
                <div v-if="filterData.length > 0" class="space-y-4">
                    <ActivityCard v-for="activity in filterData" :activity="activity" :categories="categories" :key="activity.id" @editActivity="editActivityHandle" @deleteActivity="deleteActivityHandle" @updatedActivity="handleUpdateActivty" />
                </div>
                <div v-else-if="filter == 'all' ">
                    <WelcomeHero />
                </div>
                <div v-else-if="filter != 'all' ">
                    <NoActivity />
                </div>
            </div>
            <!-- End Activity section -->
        </section>
        
    </div>
</template>

<script>
import { fetchCategories } from "@/api";
import ActivityForm from "@/components/ActivityForm";
import TheNavber from "@/components/TheNavber";
import ActivityCard from "@/components/ActivityCard";
import NoActivity from "@/components/NoActivity";
import WelcomeHero from "@/components/WelcomeHero";
import { organizeNewActivity } from "@/api";

export default {
    name: 'App',
    components: {
        ActivityForm,
        TheNavber,
        ActivityCard,
        NoActivity,
        WelcomeHero
    },
    data() {
        return {
            error: false,
            filter: "all",
            users: {},
            categories: [],
            activities: JSON.parse(localStorage.getItem('activity')) ? JSON.parse(localStorage.getItem('activity')) : [],
        }
    },
    created() {
        /*fetchActivities()
            .then(activities => this.activities = activities)
            .catch(err => this.error = err)*/

        this.categories = fetchCategories();
    },
    computed: {
        filterData() {
            if (this.filter === "inProgress") {
                return this.activities.filter((item => item.progress > 0 && item.progress < 100))
            } else if (this.filter === "notStarted") {
                return this.activities.filter((item => item.progress == 0))
            } else if (this.filter === "finished") {
                return this.activities.filter((item => item.progress == 100))
            }
            return this.activities
        }
    },
    methods: {
        filterActivityHandler(type) {
            if (type === 'all') {
                this.filter = 'all'
            } else if (type === 'inProgress') {
                this.filter = 'inProgress'
            } else if (type === 'finished') {
                this.filter = 'finished'
            } else if (type === 'notStarted') {
                this.filter = 'notStarted'
            }
        },
        setDataToLocalStorage(data) {
            localStorage.setItem('activity', JSON.stringify(data))
        },
        activitiesLength() {
            return Object.keys(this.activities).length;
        },
        activityMessage() {
            if (this.activitiesLength() > 0) {
                return ""
            } else {
                return "No activity today"
            }
        },
        createdActivity(newActivity) {
            this.activities = [...this.activities, organizeNewActivity(newActivity)];
            this.setDataToLocalStorage(this.activities)
        },
        deleteActivityHandle(obj) {
            this.activities = this.activities.filter(item => item.id !== obj.id)
            this.setDataToLocalStorage(this.activities)
        },
        handleUpdateActivty(updatedActivity) {
            this.activities = this.activities.map(item => {
                if (item.id == updatedActivity.id) {
                    return {
                        ...item,
                        title: updatedActivity.title,
                        description: updatedActivity.description,
                        progress: updatedActivity.progress,
                        category: updatedActivity.category,
                        updatedAt: new Date(),
                    }
                }
                return item
            })
            this.setDataToLocalStorage(this.activities)
        }
    }
}
</script>
<style>
* {
    transition: all 0.50s;
    font-family: 'Montserrat', sans-serif;
}
</style>