<template>
    <div>
        <div v-if="!editActivityMode" class="relative bg-white p-4 space-y-3 shadow-lg hover:shadow-2xl rounded-lg">
            <h3 class="text-xl font-semibold w-2/3 break-words">{{ activity.title }} <span class="text-xs text-gray-600 font-normal">Last update {{ activity.updatedAt | fromNow }}</span></h3>
            <p class="text-sm text-green-700 w-2/3 break-words">{{ activity.description }}</p>
            <div class="flex flex-col space-y-4 md:space-y-0 md:flex-row md:items-center md:justify-between">
                <div class="flex flex-col md:flex-row md:items-center md:space-x-2">
                    <div class="flex flex-col space-y-2 md:flex-row md:space-x-2 md:space-y-0 md:items-center">
                        <span class="text-blue-800 font-semibold">{{ TextUtility_capitalizedText(activity.category) }}</span>
                        <span class="text-gray-600 text-xs">Created at {{ activity.createdAt | prettyTime }}</span>
                    </div>
                </div>
                <div class="text-gray-600 text-sm">
                    Progress: <span class="font-semibold text-white px-1 rounded-lg" :class="'bg-' + colorProgress + '-600' ">
                        {{ activity.progress }}<span class="font-bold">%</span> </span>
                </div>
                <!-- This is another way to do that -->
                <!-- <div class="text-gray-600 text-sm">
            Progress: <span :style="{'color':colorProgress}">{{ activity.progress }}%</span>
          </div> -->
            </div>
            <div @click="settingDisplayed = !settingDisplayed" class="absolute top-0 right-0 pt-2 mx-4 cursor-pointer">
                <span class="hover:bg-gray-200 p-2 inline-block rounded-t-lg rounded-l-lg rounded-r-lg rounded-b-none">
                    <svg class="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37 2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z"></path>
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"></path>
                    </svg>
                </span>
            </div>
            <div v-if="settingDisplayed" class="flex space-x-2">
                <span @click="editActivity" class="cursor-pointer text-sm tracking-wider bg-gray-200 p-2 rounded-lg">
                    <svg class="w-6 h-6 text-indigo-600" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z"></path>
                    </svg>
                </span>
                <span @click="$emit('deleteActivity',activity)" class="cursor-pointer text-sm tracking-wider bg-gray-200 p-2 rounded-lg">
                    <svg class="w-6 h-6 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"></path>
                    </svg>
                </span>
            </div>
        </div>
        <div v-else class="bg-white p-4 mb-4 rounded-lg shadow-lg">
            <EditActivity :categories="categories" :activity="activity" @editMode="changeEditMode" @updatedActivity="updateActivity" />
        </div>
    </div>
</template>

<script>
import TextUtility from "@/mixins/TextUtility.vue";
import EditActivity from "@/components/EditActivity";

export default {
    name: 'ActivityCard',
    components: {
        EditActivity,
    },
    /* Make sure to asign it top from the props */
    mixins: [TextUtility],
    props: ["activity", "categories"],
    data() {
        return {
            settingDisplayed: false,
            editActivityMode: false,
        }
    },
    computed: {
        colorProgress() {

            const progress = this.activity.progress

            if (progress < 50) {
                return "red"
            } else if (progress < 100) {
                return "yellow"
            } else {
                return "green"
            }
        }
    },
    methods: {

        /* Another way to pass data into parents component's */
        editActivity(id) {
            this.editActivityMode = true
            this.$emit('editActivity', id)
        },
        changeEditMode(type) {
            this.editActivityMode = type
        },
        updateActivity(a) {
            this.$emit('updatedActivity', a)
        }

    }
}
</script>