import Vue from 'vue'
import App from './App.vue'
import router from './router'
import moment from 'moment'

Vue.config.productionTip = false

/* This is called filter which can use anywhere in the vue app by | sign */
Vue.filter('uppercase',(string)=> {
	if(!string){
		return "";
	}
	return string.toUpperCase();
})

Vue.filter('prettyTime',(timeStamp)=> {
	if(!timeStamp){
		return "";
	}
	return moment(timeStamp).format('lll');
})

Vue.filter('fromNow',(timeStamp)=> {
	if(!timeStamp){
		return "";
	}
	return moment(timeStamp).startOf('minutes').fromNow();
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
