<template>
    <nav class="bg-green-200 px-4 pt-4 md:px-16 md:pt-8 space-y-4">
        <h1 class="text-4xl font-semibold py-4 text-green-900">Eat The Elephant</h1>
        <div class="flex items-center">
            <span @click="clickNav('all')" class="cursor-pointer hover:bg-green-300 rounded-t-lg px-4 py-2" :class="{ 'bg-green-300': all }">All</span>
            <span @click="clickNav('inProgress')" class="cursor-pointer hover:bg-green-300 rounded-t-lg px-4 py-2" :class="{ 'bg-green-300': inProgress }">In Progress</span>
            <span @click="clickNav('finished')" class="cursor-pointer hover:bg-green-300 rounded-t-lg px-4 py-2" :class="{ 'bg-green-300': finished }">Finished</span>
            <span @click="clickNav('notStarted')" class="cursor-pointer hover:bg-green-300 rounded-t-lg px-4 py-2" :class="{ 'bg-green-300': notStarted }">Not started</span>
        </div>
    </nav>
</template>

<script>
export default {

    name: 'TheNavber',
    data() {
        return {
            all: true,
            inProgress: false,
            finished: false,
            notStarted: false
        };
    },
    methods: {
        clickNav(type) {
            if (type === 'all') {
                this.$emit('filterActivity', 'all')
                this.all = true,
                    this.inProgress = false,
                    this.finished = false
                this.notStarted = false
            } else if (type === 'inProgress') {
                this.$emit('filterActivity', 'inProgress')
                this.all = false
                this.inProgress = true
                this.finished = false
                this.notStarted = false
            } else if (type === 'finished') {
                this.$emit('filterActivity', 'finished')
                this.all = false
                this.inProgress = false
                this.finished = true
                this.notStarted = false
            } else if (type === 'notStarted') {
                this.$emit('filterActivity', 'notStarted')
                this.all = false
                this.inProgress = false
                this.finished = false
                this.notStarted = true
            }
        }
    }

}
</script>