export const fetchActivities = () => {
    return new Promise((resolve, reject) => {
        if (!activityData) {
            reject("Server error");
        }
        setTimeout(() => {
            resolve(activityData())
        }, 1500)
    })
}

export const ganerateUId = () => Math.floor(new Date() * Math.random())

export const organizeNewActivity = ({ title, description, category }) => {
    return {
        id: ganerateUId(),
        title: title,
        description: description,
        category: category,
        progress: 0,
        createdAt: new Date(),
        updatedAt: new Date(),
    }
}

