<template>
    <div>
        <form class="space-y-2">
            <div class="flex flex-col">
                <label class="text-sm text-green-900 font-semibold" for="title">Title</label>
                <input v-model="currentActivity.title" class="px-4 py-2 rounded-lg focus:outline-none bg-green-300 text-green-900 placeholder-green-900" type="text" id="title" placeholder="Title...">
            </div>
            <div class="flex flex-col">
                <label class="text-sm text-green-900 font-semibold" for="title">Description</label>
                <textarea v-model="currentActivity.description" class="px-4 py-2 rounded-lg focus:outline-none bg-green-300 text-green-900 placeholder-green-900" id="description" cols="30" rows="3" placeholder="Description..."></textarea>
            </div>
            <div class="flex flex-col">
                <label class="text-sm text-green-900 font-semibold" for="category">Category</label>
                <select v-model="currentActivity.category" class="px-4 py-2 bg-green-300 rounded-lg focus:outline-none">
                    <option v-for="category in categories" :key="category.id">{{ category.name }}</option>
                </select>
            </div>
			<div class="flex space-x-2 items-center">
				<label for="progress">Progress</label>
				<input v-model="currentActivity.progress" type="range">
				<span class="text-green-900">{{ currentActivity.progress }}%</span>
			</div>
            <button @click.prevent="submitForm()" class="focus:outline-none bg-green-600 text-green-100 px-4 py-2 rounded-lg font-semibold">Edit activity</button>
            <button class="focus:outline-none bg-white text-green-800 px-4 py-2 rounded-lg border border-green-600  font-semibold ml-2" @click.prevent="$emit('editMode',false)">Cancle</button>
        </form>
    </div>
</template>

<script>
export default {

    name: 'EditActivity',
    props: ["categories", "activity"],
    data() {
        return {
            currentActivity: {
				id: this.activity.id,
                title: this.activity.title,
                description: this.activity.description,
                category: this.activity.category,
                progress: this.activity.progress,
            }
        }
    },
    methods: {
        submitForm() {
            this.$emit('editMode',false)
            this.$emit('updatedActivity',this.currentActivity)
        },
        categoryHandler(e) {
			this.currentActivity.category = e.target.value
        }
    }
}
</script>