<template>
    <div class="col-span-1 mb-4">
        <div v-if="!layout.isFormDispalyed" @click="layout.isFormDispalyed = !layout.isFormDispalyed" class="text-center bg-green-600 text-green-100 px-4 py-2 cursor-pointer font-semibold rounded-lg text-white">New Activity</div>
        <form v-if="layout.isFormDispalyed" class="space-y-2">
            <div class="flex flex-col">
                <label class="text-sm text-green-900 font-semibold" for="title">Title</label>
                <input v-model="newActivity.title" class="px-4 py-2 rounded-lg focus:outline-none" type="text" id="title" placeholder="Title...">
            </div>
            <div class="flex flex-col">
                <label class="text-sm text-green-900 font-semibold" for="title">Description</label>
                <textarea v-model="newActivity.description" class="px-4 py-2 rounded-lg focus:outline-none" id="description" cols="30" rows="3" placeholder="Description..."></textarea>
            </div>
            <div class="flex flex-col">
                <label class="text-sm text-green-900 font-semibold" for="category">Category</label>
                <select v-model="newActivity.category" class="px-4 py-2 rounded-lg focus:outline-none">
                    <option disabled value="">Select a category</option>
                    <option v-for="category in categories" :key="category.id">{{ category.name }}</option>
                </select>
            </div>
            <button @click.prevent="createActivity" :disabled="!isFormValid()" :class="{ 'opacity-50 cursor-not-allowed' : 
                    !isFormValid() }" class="focus:outline-none bg-green-600 text-green-100 px-4 py-2 rounded-lg font-semibold">Create activity</button>
            <button @click.prevent="layout.isFormDispalyed = !layout.isFormDispalyed" class="focus:outline-none bg-white text-green-800 px-4 py-2 rounded-lg font-semibold ml-2">Cancle</button>
        </form>
    </div>
</template>

<script>
export default {
    name: 'ActivityForm',
    props: ["categories"],
    data() {
        return {
            layout: {
                isFormDispalyed: false,
            },
            newActivity: {
                title: "",
                description: "",
                category: ""
            }
        }
    },
    methods: {
        isFormValid() {
            return this.newActivity.title && this.newActivity.description && this.newActivity.category
        },
        createActivity() {
            this.$emit('createActivity', this.newActivity);
            this.newActivity = {
                title: "",
                description: "",
                category: ""
            }
            this.layout.isFormDispalyed = false;
        },
    }
}
</script>